import React, { Component } from 'react'
import Form from 'react-bootstrap/Form'
// import {InputGroup} from 'react-bootstrap/InputGroup'
import { InputGroup, FormControl } from 'react-bootstrap';

export class ContactFilter extends Component {
    state = {
        filterBy: null
    }
    componentDidMount() {
        this.setState({ filterBy: { ...this.props.filterBy } })
    }
    handleChange = ({ target }) => {
        const field = target.name
        let value = target.value
        switch (target.type) {
            case 'phone':
                value = +value
                break;
        }

        this.setState(
            ({ filterBy }) => ({ filterBy: { ...filterBy, [field]: value } }),
            () => this.props.onChangeFilter(this.state.filterBy)
        )

    }

    render() {
        if (!this.state.filterBy) return <div>Loading...</div>
        const { name, phone, } = this.state
        return (
            <form className='contact-filter'>

                <section>
                    <InputGroup className="mb-3">
                        <InputGroup.Text htmlFor="name" id="inputGroup-sizing-default">
                            Search by name
                        </InputGroup.Text>
                        <Form.Control
                            onChange={this.handleChange} value={name} phone="name" name="name" id="name"
                            aria-label="Default"
                            aria-describedby="inputGroup-sizing-default"
                            autocomplete="off"

                        />
                    </InputGroup>
                </section>

                <section>
                    <InputGroup className="mb-3">
                        <InputGroup.Text htmlFor="phone" id="inputGroup-sizing-default">
                            Search by phone
                        </InputGroup.Text>
                        <Form.Control
                            onChange={this.handleChange} value={phone} phone="text" name="phone" id="phone"
                            aria-label="Default"
                            aria-describedby="inputGroup-sizing-default"
                            autocomplete="off"

                        />
                    </InputGroup>




                </section>
            </form>)



    }
}
