import {StorageService} from './storage.service.js'
import { UserService } from './user.service.js'

import axios from 'axios'

export const BitcoinService = {
    getRate,
    getMarketPrice,
    getConfirmedTransactions,
}

const MP_KEY = 'mpDB'
const TD_KEY = 'tdDB'


let gMarketPrice = StorageService.loadFromStorage(MP_KEY) || {}
let gTransPerDay= StorageService.loadFromStorage(TD_KEY) || {}


async function  getRate() {
    const url = `https://blockchain.info/tobtc?currency=USD&value=${UserService.getUser().coins}`
    const {data}= await axios.get(url)
    return data
  }

  async function getMarketPrice() {
    const url =`https://api.blockchain.info/charts/market-price?timespan=1months&format=json&cors=true`
    if (gMarketPrice[MP_KEY]) return Promise.resolve(gMarketPrice[MP_KEY])
    const {data}= await axios.get(url)
    if (!gMarketPrice && !gMarketPrice.length) throw new Error('No info Found')
    gMarketPrice[MP_KEY]=data
    StorageService.saveToStorage(MP_KEY, gMarketPrice)
    console.log(data);
    return data

}

async function getConfirmedTransactions() {
    const url =`https://api.blockchain.info/charts/transactions-per-second?timespan=1months&format=json&cors=true`
    if (gTransPerDay[TD_KEY]) return Promise.resolve(gTransPerDay[TD_KEY])
    const {data}= await axios.get(url)
    if (!gTransPerDay && !gTransPerDay.length) throw new Error('No info Found')
    gTransPerDay[TD_KEY]=data
    StorageService.saveToStorage(TD_KEY, gTransPerDay)
    console.log(data);

    return data
}


