

import React, { Component } from 'react'
import { BitcoinService } from '../services/bitcoin.service'
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
import { Line } from 'react-chartjs-2';
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);


export class Charts extends Component {
  state = {
    marketPrice: null,
    transactions: null,

    
  }

  marketPriceChart = () =>  {
    return {
      labels: this.state.marketPrice.values.map(value => (new Date(value.x)).toLocaleString()),
      datasets: [{
        label: this.state.marketPrice.name,
        data: this.state.marketPrice.values.map(value => value.y),
        fill: false,
        borderColor: 'rgb(75, 192, 192)',
        tension: 0.1,
        backgroundColor:'rgb(8, 83, 100)'

      }]
    }
  } 

  transactionsChart = () =>  {
    return {
      labels: this.state.transactions.values.map(value => (new Date(value.x)).toLocaleString()),
      datasets: [{
        label: this.state.transactions.name,
        data: this.state.transactions.values.map(value => value.y),
        fill: 'red',
        borderColor: 'rgb(75, 192, 192)',
        tension: 0.1,
        backgroundColor:'rgb(8, 83, 100)',

      }],
      // options: {
      //   plugins: {
      //     customCanvasBackgroundColor: {
      //       color: 'red',
      //     }
      //   }
      // },
    }
  } 

 

  async componentDidMount() {
    const marketPrice = await BitcoinService.getMarketPrice()
    const transactions = await BitcoinService.getConfirmedTransactions()
    console.log(marketPrice.values, transactions);
    this.setState({ marketPrice, transactions });

  }

  optionsChart = () =>  {
    return {
 
    plugins: {
      customCanvasBackgroundColor: {
        color: 'red',
      },
    },
    scales: {x: {ticks: {display: false}}}}
  }

  render() {
    if (!this.state.marketPrice || !this.state.transactions) {
      return <div>Loading...</div>;
    }
    return (
      <>
      <Line  data={this.marketPriceChart()}   options={{scales: {x: {ticks: {display: false}}}}}/>
      <div className='info-chart'>Average USD market price across major bitcoin exhanges </div>
      <Line data={this.transactionsChart()} options={this.optionsChart() }/>
      <div className='info-chart'>The number of daily confirmed Bitcoin transactions </div>
      </>)



  }
}

