import Button from 'react-bootstrap/Button'
import 'bootstrap/dist/css/bootstrap.min.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
// import { faTrash } from '@fortawesome/free-solid-svg-icons';
import { Link } from 'react-router-dom'

export function ContactPreview({ contact, onRemoveContact }) {


    return (
        <article className='contact-preview'>
                <Link to={`/contact/${contact._id}`} className="info">
                    <img src={`https://robohash.org/set_set5/bgset_bg1/${contact._id}?size=300x300`} alt={contact.name} />
                    <h2>{contact.name}</h2>
                </Link>

            <section className="actions">
                <Button className="delete-btn" variant="outline-secondary" onClick={() => onRemoveContact(contact._id)} >Delete</Button>
                <Link to={`/contact/edit/${contact._id}`}><Button className="edit-btn" variant="outline-secondary" >Edit</Button></Link>
                

                <button><FontAwesomeIcon icon="fa-solid fa-trash" /></button>
                {/* <button><FontAwesomeIcon icon={faTrash} /></button> */}
                <button> <FontAwesomeIcon icon="fa-solid fa-trash" /></button>

            </section>

        </article>
    )
}
