import React, { Component } from 'react'
import { UserService } from '../services/user.service'
import { BitcoinService } from '../services/bitcoin.service'


export class HomePage extends Component {
    state = {
        user: null,
        bitcoinRate: null,
    }

     componentDidMount() {
        this.loadUser()
        this.loadbitcoinRate()
    }

    loadUser = async () => {
        try {
            const user = await UserService.getUser()
            this.setState({ user })
        } catch (error) {
            console.log('error:', error)
        }
    }

    loadbitcoinRate = async () => {
        try {
            const bitcoinRate = await BitcoinService.getRate()
            this.setState({ bitcoinRate })
        } catch (error) {
            console.log('error:', error)
        }
    }




    //   BitcoinService.getRate()
    // .then(rate => {
    //   this.setState({ bitcoinRate: rate })
    // })

    // calcBitcoin = () => {
    //     return this.state.bitcoinRate * this.state.user.coins

    // }

    render() {
        const { user,bitcoinRate } = this.state
        if (!user||!bitcoinRate) return <div>Loading...</div>
        return (
            <>
            <section className="home-page">   
            
            <h1> Hello {this.state.user.name} !</h1>
                <div className="coins-info flex ">
                <img src={require('../img/coins.png')}/>
                <div>Coins: {this.state.user.coins}</div>
                </div>
                <div className="bitcoin-info flex ">
                <img src={require('../img/bitcoin.png')}/>
                <div>BTC:{this.state.bitcoinRate} </div>
                </div>
            
                     </section>

        
            </>
        )
    }
}
